import * as THREE from 'three';
import { BufferGeometryUtils } from 'three/examples/jsm/utils/BufferGeometryUtils';

import { EOrbitalType } from '../models/EOrbitalType';
import { Orbit } from '../utils/orbit';
import { AbstractToyModel } from '../abstract-scene/abstract-toy-model';
import { IZoomable } from '../models/IZoomable';
import { createAsteroidGeometry } from '../utils/create-asteroid-geometry';
import { getTextureFromImageUrl } from '../utils/get-texture-from-image-url';
import { imageBaseUrl } from '../utils/constants';
import { SKEphem } from '../utils/sk-ephem';
import { auToMeters } from '../utils/conversions';
import { getPlanetRadiusMeters } from '../utils/get-planet-radius-meters';
import { IAsteroidDatum } from '../models/IAsteroidDatum';

import mba_data from '../data/json/asteroids/asteroids-MBA-h-11.json';
import neo1km_data from '../data/json/asteroids/asteroids-1kmNEO-h-20.json';
import pha_data from '../data/json/asteroids/asteroids-PHA-h-999.json';

console.log(neo1km_data);

const au = auToMeters(1);

type TAsteroidType = 'MBA' | 'NEO1KM' | 'PHA';

type TData = { [K in TAsteroidType]: IAsteroidDatum[] };

const asteroidData: TData = {
  MBA: mba_data,
  NEO1KM: neo1km_data,
  PHA: pha_data,
};

export class AsteroidBelt extends AbstractToyModel implements IZoomable {
  // --->>>

  private orbits: Orbit[] = [];
  private radius = getPlanetRadiusMeters('CERES') / 2;
  private geometries: {
    geometry: THREE.BufferGeometry;
    x: number;
    y: number;
    z: number;
  }[] = [];
  private groupedMeshes = new THREE.Group();
  private mergedMesh!: THREE.Mesh<
    THREE.BufferGeometry,
    THREE.MeshPhongMaterial
  >;
  private lastDist = 0;
  private isAdaptiveAsteroids = true;

  constructor(public readonly NAME: string, private belt: TAsteroidType) {
    super(3000);
    this.groupedMeshes.name = 'GROUP OF MESHES';
  }

  async init() {
    return new Promise<THREE.Group>(async resolve => {
      // --->>

      // const url = `${imageBaseUrl}/misc/asteroid-texture-1024.jpg`;
      const url = `${imageBaseUrl}/misc/rock-texture-512.png`;
      const texture = await getTextureFromImageUrl(url).catch(_ => null);
      const color = getColor(this.belt);

      const data = asteroidData[this.belt] as IAsteroidDatum[];
      data.forEach(datum => {
        // Filter on H
        const { H } = datum;
        if (H <= 0) return;

        // Create orbit
        const orbit = getOrbit(datum, 'red', 0.01);
        this.orbits.push(orbit);
        // this._sceneEntityGroup.add(orbit.getProjectedOrbitLine());
        const { x, y, z } = orbit.getXyzMeters();

        if (!x || !y || !z) {
          console.log('>>>>', datum.name, x, y, z);
          return;
        }

        // Create geometry
        const r = ((this.radius * 15) / (H + 1)) * (0.1 + 5 * Math.random());
        if (!r || r <= 0) console.log('!!!!!!!!!!', datum.name);
        const geometry = createAsteroidGeometry(
          r,
          0.25 * (1.5 - Math.random())
        );
        geometry.name = datum.name || 'unknown';
        this.geometries.push({ geometry, x, y, z });

        // Create mesh
        const mesh = new THREE.Mesh(
          geometry,
          new THREE.MeshPhongMaterial({
            color: new THREE.Color(color),
            map: texture,
            shininess: 0,
            transparent: true,
          })
        );

        // Wrap mesh in toy-scalable group
        const wrapper = new THREE.Group();
        wrapper.add(mesh);
        wrapper.position.set(x, y, z);
        this.groupedMeshes.add(wrapper);
        this._toyGroup.push(wrapper);
      });

      // Merge meshes
      this.mergedMesh = new THREE.Mesh(
        this.getMergedGeometries(),
        new THREE.MeshPhongMaterial({
          // color: new THREE.Color('grey'),
          color: new THREE.Color(color),
          map: texture,
          shininess: 0,
          transparent: true,
        })
      );
      this.mergedMesh.geometry.name = 'mnerged geeom';
      // console.log(
      //   'this.mergedMesh',
      //   this.mergedMesh.geometry.groups.forEach(group =>
      //     console.log('>>> ' + JSON.stringify(group), group)
      //   )
      // );

      // Toggle between grouped and merged meshes
      this.groupedMeshes.visible = this.isAdaptiveAsteroids;
      this.mergedMesh.visible = !this.isAdaptiveAsteroids;

      // Finish
      this._sceneEntityGroup.add(this.mergedMesh);
      this._sceneEntityGroup.add(this.groupedMeshes);
      resolve(this._sceneEntityGroup);
    });
  }

  getMergedGeometries() {
    return BufferGeometryUtils.mergeBufferGeometries(
      this.geometries.map(el => {
        const { geometry, x, y, z } = el;
        const newGeometry = geometry.clone();
        const s = 1000;
        newGeometry.scale(s, s, s);
        newGeometry.translate(x, y, z);
        return newGeometry;
      }),
      true
    );
  }

  setIsAdaptiveAsteroid(val: boolean) {
    this.isAdaptiveAsteroids = val;
    this.groupedMeshes.visible = this.isAdaptiveAsteroids;
    this.mergedMesh.visible = !this.isAdaptiveAsteroids;
  }

  setIsZoomToToyScale(isToyScale: boolean) {
    //
    // this.groupedMeshes.visible = !isToyScale;
    // this.mergedMesh.visible = !!isToyScale;

    // this.groupedMeshes.visible = true;
    // this.mergedMesh.visible = false;

    super.setIsZoomToToyScale(isToyScale);
  }

  public getPosition = () => {
    return new THREE.Vector3();
  };

  public getRadius = () => this.radius;

  update(_camera?: THREE.Camera) {
    // Toy Model Scale
    this._updateModelScale();

    if (!_camera) return;
    const dist = _camera.position.distanceTo(new THREE.Vector3());
    const isUpdate = Math.abs(this.lastDist - dist) > 10 * au;

    // Scale opacity to peak when camera is in belt, and then
    // tend toward zero when moving away; takes a bit of tuning
    // in the exponential if you change asteroid size
    const p = 2.8 * au; // 'Peak' distance to belt based on Ceres
    const opacity = 1 / Math.abs((dist - p) / p) ** 1.1;
    // this.mergedMesh.material.opacity = opacity;
    // this.mergedMesh.material.needsUpdate = true;

    const is = !true;

    if (isUpdate && is) {
      //

      console.log('!!!');
      const s = dist / au;
      // const s = 2;
      const newGeometries = this.geometries.map(child => {
        const { geometry, x, y, z } = child;
        const newGeometry = geometry.clone();
        newGeometry.translate(-x, -y, -z);
        newGeometry.scale(s, s, s);
        newGeometry.translate(x, y, z);
        return newGeometry;
      });
      // this.mergedMesh.geometry = this.getMergedGeometries();
      this.mergedMesh.geometry = BufferGeometryUtils.mergeBufferGeometries(
        newGeometries,
        true
      );

      this.lastDist = dist;
    }

    if (this.isAdaptiveAsteroids) {
      // console.log('xxx');
      this.groupedMeshes.traverse(child => {
        // console.log(child);
        if (child instanceof THREE.Mesh) {
          // --->

          // Scaling size linearly with dist <=> belt visibility ~constant
          // const mu = 1;
          // const s = dist ** mu / au;

          const s = (dist / au) ** 1.5 * 0.1;
          // console.log('s', s);
          child.scale.set(s, s, s);

          // Scale opacity to peak when camera is in belt, and then
          // tend toward zero when moving away; takes a bit of tuning
          // in the exponential if you change asteroid size
          const p = 2.8 * au; // 'Peak' distance to belt based on Ceres
          const opacity = 1 / Math.abs((dist - p) / p) ** 1.1;
          child.material.opacity = opacity;
          child.material.needsUpdate = true;
          // if (Math.random() < 0.00001) console.log('dist', dist / au, s, opacity);
        }
      });
    }
  }
}

function getSKEphem(datum: any) {
  const { epoch, a, e, i, om, w, ma } = datum;
  return new SKEphem({ epoch, a, e, i, om, w, ma }, 'deg', true);
}

function getOrbit(datum: IAsteroidDatum, color: string, opacity: number) {
  return new Orbit(
    datum.desig,
    EOrbitalType.ASTEROID,
    getSKEphem(datum),
    color,
    opacity
  );
}

function getColor(belt: TAsteroidType) {
  switch (belt) {
    case 'MBA': {
      return 'grey';
    }
    case 'NEO1KM': {
      return 'orange';
    }
    case 'PHA': {
      return 'red';
    }
  }
}
